import Vue from '@/components/base'
import { Component } from 'vue-property-decorator'
import template from './CustomUpload.vue'

import CustomFileItem from '../CustomFileItem'
import Upload from '@/components/Upload'
import { ElUploadInternalFileDetail } from 'element-ui/types/upload'

@Component({
    name: 'CustomUpload',
    mixins: [template],
    components: {
        CustomFileItem,
        Upload
    }
})
export default class CustomUpload extends Vue {
    public $refs!: {
        upload: Upload
    }
    //#region datas ******************************/
    //#endregion datas ***************************/

    //#region props ******************************/
    //#endregion props ***************************/

    //#region vuex *******************************/
    //#endregion vuex ****************************/

    //#region computed ***************************/
    //#endregion computed ************************/

    //#region watchers ***************************/
    //#endregion watchers ************************/

    //#region life cycle *************************/
    protected created() {}
    //#endregion life cycle **********************/

    //#region hooks ******************************/
    //#endregion hooks ***************************/

    //#region event handler **********************/
    protected handleRemove(file: ElUploadInternalFileDetail) {
        this.$refs.upload.removeFile(file)
    }

    protected handlePreview(file: ElUploadInternalFileDetail) {
        this.$refs.upload.preview(file)
    }
    //#endregion event handler *******************/

    //#region methods ****************************/
    //#endregion methods *************************/
}
