/**
 * Upload 上传组件
 * @author minfive <chenxiaowu1994@outlook.com>
 * @date 2019-07-23 17:21:33
 * @since 0.0.0
 */

import Vue from '@/components/base'
import { Component, Prop, Watch } from 'vue-property-decorator'
import template from './Upload.vue'
import YzUploadItem, {
    UploadType,
    BaseActionCommand,
    ActionItem
} from './UploadItem'
import CustomDraggable from './CustomDraggable'

import { Upload, Image } from 'element-ui'
import Emitter from 'element-ui/lib/mixins/emitter'
import { ElUploadInternalFileDetail } from 'element-ui/types/upload'
import {
    getFileNameByUrl,
    getFileSize,
    imageResize,
    sleep,
    resetExifOrientation
} from './util'

import previewSvg from '@icon/zoom.svg'
import deleteSvg from '@icon/delete.svg'

export * from './UploadItem'

type StatusType = 'success' | 'fail' | 'uploading' | 'ready'

interface ValueType {
    name: string
    url: string
}

export type FileType = 'image' | 'video' | 'audio'

interface FileListItem {
    name: string
    url: string
    localUrl?: string
    status: StatusType
    uid: number
    type: FileType
}

type OnCropType = (file: File) => Promise<File>

const defaultActions: ActionItem[] = [
    {
        icon: previewSvg,
        command: BaseActionCommand.Preview
    },
    {
        icon: deleteSvg,
        command: BaseActionCommand.Delete
    }
]

@Component({
    name: 'YzUpload',
    inheritAttrs: false,
    mixins: [template, Emitter],
    components: {
        YzUploadItem,
        // @ts-ignore
        ElImageViewer: Image.components.ImageViewer
    }
})
export default class YzUpload extends Vue {
    public $refs!: {
        elUpload: any
        imageViewer: any
    }

    //#region datas ******************************/
    protected fileList: FileListItem[] = []

    protected previewDialog = {
        status: false,
        img: '',
        zIndex: 2000
    }

    // 当前上传状态更新文件
    protected updateProgressFile: ElUploadInternalFileDetail | null = null

    /* 上传钩子 */
    protected onExceed!: Upload['onExceed']
    protected onPreview!: Upload['onPreview']
    protected onRemove!: Upload['onPreview']
    protected onSuccess!: Upload['onSuccess']
    protected onError!: Upload['onError']
    protected onProgress!: Upload['onProgress']
    protected beforeUpload!: Upload['beforeUpload']
    //#endregion datas ***************************/

    //#region props ******************************/
    /**
     * 当前上传文件列表
     */
    @Prop({ default: () => [] })
    protected readonly value!: Array<string | { name: string; url: string }>

    /**
     * 上传文件列表简洁模式（纯链接模式）
     * 备注：简洁模式下，文件名取至 url
     */
    @Prop({ default: true, type: Boolean })
    protected readonly isSimpleValue!: boolean

    /**
     * 上传组件类型
     * 默认为文件类型，根据不同的类型会有不同的上传结果展示
     */
    @Prop({ default: 'file' })
    protected readonly type!: UploadType | UploadType[]

    /**
     * 是否多选
     */
    @Prop({ default: false, type: Boolean })
    protected readonly multiple!: boolean

    /**
     * 是否显示文件列表
     */
    @Prop({ default: true, type: Boolean })
    protected readonly showFileList!: boolean

    /**
     * 是否启用拖拽上传
     * 启用拖拽上传时，type 会被重置为 file 类型
     */
    @Prop({ default: false, type: Boolean })
    protected readonly drag!: boolean

    /**
     * 上传文件大小限制
     */
    @Prop({ default: null })
    protected readonly maxSize!: number | null

    /**
     * 上传图片压缩阀值
     * 设为 0 时不做处理，单位：MB
     */
    @Prop({ default: 10 })
    protected readonly compression!: number

    /**
     * 数量限制
     */
    @Prop({ default: 1 })
    protected readonly limit!: number

    /**
     * 禁用
     */
    @Prop({ default: false, type: Boolean })
    protected readonly disabled!: boolean

    /**
     * 文件接受类型
     * 为空数组则表示全部接受
     */
    @Prop({ default: () => [], type: Array })
    protected readonly acceptType!: string[]

    /**
     * 上传结果项操作配置
     */
    @Prop({ default: () => defaultActions, type: Array })
    protected readonly itemActions!: ActionItem[]

    /**
     * 是否对图片进行裁剪
     */
    @Prop({ default: false, type: Boolean })
    protected readonly crop!: boolean

    /**
     * 裁剪处理函数
     */
    @Prop({ default: null, type: Function })
    protected readonly onCrop!: OnCropType | null

    /**
     * 层级管理器
     * 不传时默认为内置管理器
     */
    @Prop({ default: null, type: Object })
    protected readonly popupManager!: any

    /**
     * 是否支持拖拽排序
     */
    @Prop({ default: false, type: Boolean })
    protected readonly sortable!: boolean
    //#endregion props ***************************/

    //#region vuex *******************************/
    //#endregion vuex ****************************/

    //#region computed ***************************/

    protected get currentValue() {
        return this.value
    }
    protected set currentValue(val) {
        this.$emit('input', val)
    }

    protected get isImageAndVideo() {
        let type = this.currentType
        return (
            Array.isArray(type) &&
            type.includes('image') &&
            type.includes('video')
        )
    }

    protected get isImageOrVideo() {
        let type = this.currentType
        return this.isImageAndVideo || type === 'video' || type === 'image'
    }

    protected get isShowFileList() {
        if (this.isImageAndVideo) {
            return this.multiple
        }

        switch (this.currentType) {
            case 'image':
            case 'video':
                return this.multiple
            case 'file':
            default:
                return true
        }
    }

    protected get currentType() {
        // 启用拖拽上传时，重置类型为 file
        if (this.drag) {
            return 'file'
        }

        return this.type
    }

    protected get currentFileAccept() {
        if (this.isImageAndVideo) {
            return 'image/*, video/*'
        }

        switch (this.currentType) {
            case 'image':
                return 'image/*'
            case 'video':
                return 'video/*'
            case 'file':
            default:
                return 'audio/*, video/*, image/*'
        }
    }

    protected get currentListType() {
        if (this.isImageAndVideo) {
            return 'picture-card'
        }

        switch (this.currentType) {
            case 'image':
            case 'video':
                return 'picture-card'
            case 'file':
            default:
                return 'text'
        }
    }

    protected get currentAttrs() {
        const attrs = {
            accept: this.currentFileAccept,
            ...this.$attrs,
            drag: this.drag,
            limit: this.limit,
            multiple: this.multiple,
            disabled: this.disabled,
            'auto-upload': true,
            'show-file-list': this.isShowFileList,
            'file-list': this.fileList,
            'list-type': this.currentListType,
            'on-exceed': this.onExceed,
            'on-preview': this.onPreview,
            'on-remove': this.onRemove,
            'on-success': this.onSuccess,
            'on-error': this.onError,
            'on-progress': this.onProgress,
            'before-upload': this.beforeUpload
        }

        return attrs
    }

    protected get allPreviewUrlList() {
        return this.fileList.map(item => item.url)
    }

    protected get isDisabledUploadTrigger() {
        return (
            this.type === 'image' &&
            this.limit === 1 &&
            this.fileList.length === 1
        )
    }

    protected get currentItemActions() {
        return this.itemActions.filter(action => {
            return this.disabled
                ? action.command !== BaseActionCommand.Delete
                : true
        })
    }

    protected get showImageViewer() {
        return this.previewDialog.status && this.allPreviewUrlList.length > 0
    }
    //#endregion computed ************************/

    //#region watchers ***************************/
    @Watch('value', { immediate: true })
    protected changeValue() {
        const successFileList = this.value.map(item => {
            let currentFile = item as ValueType

            if (typeof item === 'string') {
                currentFile = {
                    name: getFileNameByUrl(item),
                    url: item
                }
            }

            const file = this.fileList.find(i => i.url === currentFile.url)

            return (file || {
                ...currentFile,
                uid: Math.floor(Math.random() * Date.now()),
                status: 'success'
            }) as FileListItem
        })

        successFileList.forEach(item => {
            if (!this.fileList.find(i => i.uid === item.uid)) {
                this.fileList.push(item)
            }
        })

        this.fileList = this.fileList
            .filter(item => {
                return !(
                    !successFileList.find(i => i.uid === item.uid) &&
                    item.status === 'success'
                )
            })
            .sort((first, next) => {
                if (first.status === 'success' && next.status === 'success') {
                    return (
                        successFileList.findIndex(
                            item => item.uid === first.uid
                        ) -
                        successFileList.findIndex(item => item.uid === next.uid)
                    )
                } else {
                    return 0
                }
            })

        // @ts-ignore
        this.dispatch('ElFormItem', 'el.form.change', this.value)
    }

    /**
     * 监听图片预览组件的状态，将 dom 节点植入 body 下
     * 以避免 transform fixed 问题
     */
    @Watch('showImageViewer')
    protected async changeImageViewerStatus(flag: boolean) {
        await this.$nextTick()
        if (this.$refs.imageViewer) {
            if (flag) {
                document.body.appendChild(this.$refs.imageViewer.$el)
            } else {
                document.body.removeChild(this.$refs.imageViewer.$el)
            }
        }
    }
    //#endregion watchers ************************/

    //#region life cycle *************************/
    protected created() {
        this.initHooks()
    }
    //#endregion life cycle **********************/

    //#region hooks ******************************/
    //#endregion hooks ***************************/

    //#region event handler **********************/
    protected async handleItemAction(
        action: ActionItem,
        file: ElUploadInternalFileDetail
    ) {
        if (typeof action.command === 'function') {
            action.command(file)
        } else {
            switch (action.command) {
                case BaseActionCommand.Preview:
                    this.onPreview(file)
                    break
                case BaseActionCommand.Delete:
                    this.onRemove(file)
                    break
                default:
                    console.error(
                        new TypeError(
                            `[YzUpload]: 未知操作命令 - ${action.toString()}`
                        )
                    )
            }
        }
    }

    protected closePreviewDialog() {
        this.previewDialog.status = false
    }
    //#endregion event handler *******************/

    //#region methods ****************************/
    private initHooks() {
        this.beforeUpload = async file => {
            await this.checkFileType(file)
            await this.checkFileSize(file)

            const resFile = await Promise.resolve(file)
                .then(f => this.resetExifOrientation(f))
                .then(f => this.compressionFile(f))
                .then(f => this.cropImage(f))
                .then(f => {
                    // @ts-ignore
                    f.uid = file.uid
                    return f
                })
                .then(f => this.evalUserHook('before-upload', true, f))

            // @ts-ignore
            resFile.uid = file.uid

            /**
             * 重置 elUpload 中已保存的相同 uid 项
             */
            const uploadFiles = this.$refs.elUpload
                .uploadFiles as ElUploadInternalFileDetail[]
            const target = uploadFiles.find(item => item.uid === file.uid)
            if (target) {
                target.raw = resFile
                target.url = URL.createObjectURL(resFile)
            }

            return resFile
        }

        this.onError = (err, file, fileList) => {
            this.evalUserHook('on-error', false, err, file, fileList)
        }

        this.onPreview = file => {
            if (this.checkIsImage(file.raw)) {
                this.previewImage(file)
            }
            if (this.checkIsVideo(file.raw)) {
                alert('preview video')
            }

            this.evalUserHook('on-preview', false, file)
        }

        this.onProgress = (event, file, fileList) => {
            this.updateProgressFile = file
            this.evalUserHook('on-progress', false, event, file, fileList)
        }

        this.onRemove = (file, isUploading = false) => {
            /**
             * fix 删除已上传的项时，正在上传的也被删了
             */
            const elFileList = this.$refs.elUpload.uploadFiles
            this.fileList = elFileList

            const index = this.fileList.findIndex(item => item.uid === file.uid)

            if (index !== -1) {
                this.fileList.splice(index, 1)
                this.$emit(
                    'input',
                    this.fileList
                        .filter(item => item.url !== '')
                        .map(item =>
                            this.getValue({ name: item.name, url: item.url })
                        )
                )
            }

            /**
             * 取消上传
             */
            if (isUploading) {
                this.$refs.elUpload.abort(file)
            }

            this.evalUserHook('on-remove', false, file)
        }

        this.onSuccess = (res, file, fileList) => {
            // 移除当前加载文件
            if (
                this.updateProgressFile &&
                file.uid === this.updateProgressFile.uid
            ) {
                this.updateProgressFile = null
            }

            const oldFileList = this.fileList
            const getOldFileLocalUrl = (f: ElUploadInternalFileDetail) => {
                const target = oldFileList.find(item => item.uid === f.uid)
                return f.status === 'success' || !target
                    ? f.url
                    : target.localUrl
            }

            const newFileList = fileList
                .map(item => {
                    return {
                        raw: item.raw,
                        name: item.name,
                        url: item.url || '',
                        uid: item.uid,
                        localUrl: getOldFileLocalUrl(item),
                        status: item.status,
                        type:
                            (item.raw.type.split('/')[0] as FileType) || 'image'
                    }
                })
                .slice(0, this.limit)

            this.fileList = newFileList
            // 修正当前上传成功项
            const currentFile = newFileList.find(item => item.uid === file.uid)
            const formatUrl = this.evalUserHook(
                'on-success',
                false,
                res,
                file,
                fileList
            )

            if (currentFile && formatUrl) {
                currentFile.url = formatUrl
            }

            // this.fileList = Array.from(newFileList)
            this.$emit(
                'input',
                newFileList
                    .filter(item => item.status === 'success')
                    .map(item =>
                        this.getValue({ name: item.name, url: item.url! })
                    )
            )
        }

        this.onExceed = (file, fileList) => {
            if (this.multiple && this.limit >= 1) {
                this.$notify.error(`最多上传 ${this.limit} 个文件`)
            }
            this.evalUserHook('on-exceed', false, file, fileList)
        }
    }

    protected get currentWrap() {
        return this.sortable && this.isShowFileList ? CustomDraggable : 'div'
    }

    /**
     * 检查是否可压缩
     * @param file
     */
    private checkCanCompression(file: File) {
        return file.type.includes('image') && this.compression
    }

    /**
     * 检查文件是否为图片
     * @param file
     */
    private checkIsImage(file: File) {
        return file.type.includes('image')
    }

    /**
     * 检查文件是否为图片
     * @param file
     */
    private checkIsVideo(file: File) {
        return file.type.includes('video')
    }

    /**
     * 重置图片方向
     * @param file
     */
    private resetExifOrientation(file: File) {
        if (file.type.includes('image')) {
            return resetExifOrientation(file)
        }
        return file
    }

    /**
     * 检查是否超过大小
     */
    private async checkFileSize(file: File) {
        const size = getFileSize(file)

        if (this.maxSize && size > this.maxSize) {
            this.$notify.warning(
                `文件 ${file.name} 大小超过 ${this.maxSize}M，请重新上传！`
            )
            return Promise.reject()
        }

        return Promise.resolve()
    }

    /**
     * 检查文件类型
     */
    private async checkFileType(file: File) {
        const type = file.type.replace(`${this.type}/`, '')
        const fileAcceptType = this.acceptType

        if (fileAcceptType.length > 0) {
            if (!fileAcceptType.includes(type)) {
                return Promise.reject()
            }
        }

        return Promise.resolve()
    }

    /**
     * 压缩图片
     */
    private async compressionFile(file: File) {
        const size = getFileSize(file)
        if (this.checkCanCompression(file) && size > this.compression) {
            // 压缩
            const resizeBlob = await imageResize(file, { width: 1200 })
            return new File([resizeBlob], file.name)
        }

        return file
    }

    /**
     * 预览图片
     */
    private async previewImage(file: ElUploadInternalFileDetail) {
        const url = (this.previewDialog.img = file.url || '')
        this.previewDialog.status = true
        this.popupManager &&
            (this.previewDialog.zIndex = this.popupManager.zIndex)

        /**
         * 切换当前显示图片
         */
        await sleep(10)
        this.$refs.imageViewer.index = this.allPreviewUrlList.findIndex(
            item => item === url
        )
    }

    /**
     * 裁剪图片
     */
    private async cropImage(file: File) {
        if (this.crop && this.type === 'image') {
            if (this.onCrop) {
                return this.onCrop(file)
            } else {
                // TODO: 图片裁剪
                return file
            }
        } else {
            return file
        }
    }

    /**
     * 获取对应格式值
     */
    private getValue(val: ValueType) {
        return this.isSimpleValue ? val.url : val
    }

    /**
     * 执行用户自定义钩子
     */
    private evalUserHook(hookName: string, async: boolean, ...args: any[]) {
        const hook = this.$attrs[hookName]

        if (hook) {
            if (async) {
                return new Promise(resolve => {
                    // @ts-ignore
                    hook(...args).then(resolve)
                })
            } else {
                // @ts-ignore
                return hook(...args)
            }
        }

        return null
    }

    /* tslint:disable */
    public removeFile(file: ElUploadInternalFileDetail) {
        this.onRemove(file)
    }

    public preview(file: ElUploadInternalFileDetail) {
        this.onPreview(file)
    }
    /* tslint:enable */
    //#endregion methods *************************/
}
