import * as exif from 'exif-js'

export function sleep(time: number = 0) {
    return new Promise(resolve => {
        window.setTimeout(resolve, time)
    })
}

export function getFileNameByUrl(url: string) {
    const uriObj = new URL(url)

    return uriObj.pathname.split('/').pop() as string
}

export function getFileSize(file: File) {
    return file.size / 1024 / 1024
}

export function getExifOrientation(img: HTMLImageElement) {
    return new Promise<number | undefined>((resolve, reject) => {
        // @ts-ignore
        exif.getData(img, function() {
            // @ts-ignore
            resolve(exif.getTag(this, 'Orientation'))
        })
    })
}

export function readFile(file: File) {
    return new Promise<string>((resolve, reject) => {
        let reader = new FileReader()
        reader.addEventListener('load', () => {
            // @ts-ignore
            resolve(reader.result)
        })
        reader.addEventListener('error', reject)
        reader.readAsDataURL(file)
    })
}

export function buildImage(src: string) {
    return new Promise<HTMLImageElement>((resolve, reject) => {
        let img = new Image()
        img.addEventListener('load', () => {
            resolve(img)
        })
        img.addEventListener('error', reject)
        img.src = src
    })
}

/**
 * 重置图片方向
 */
export function resetExifOrientation(file: File): Promise<File> {
    const canvas = document.createElement('canvas')

    return new Promise(async (resolve, reject) => {
        let base64Data: string | null = await readFile(file)
        let img: HTMLImageElement | null = await buildImage(base64Data)

        if (!img.width || !img.height) {
            return reject(new Error('img error'))
        }

        canvas.width = img.width
        canvas.height = img.height
        const ctx = canvas.getContext('2d')

        let drawWidth = canvas.width
        let drawHeight = canvas.height
        let imgExifOrientation
        try {
            imgExifOrientation = await getExifOrientation(img)
        } catch (error) {}

        if (imgExifOrientation !== undefined) {
            let degree = 0
            let width = canvas.width
            let height = canvas.height
            switch (imgExifOrientation) {
                case 3:
                    degree = 180
                    drawWidth = -width
                    drawHeight = -height
                    break
                case 6:
                    canvas.width = height
                    canvas.height = width
                    degree = 90
                    drawWidth = width
                    drawHeight = -height
                    break
                case 8:
                    canvas.width = height
                    canvas.height = width
                    degree = 270
                    drawWidth = -width
                    drawHeight = height
                    break
            }
            if (degree) {
                ctx && ctx.rotate((degree * Math.PI) / 180)
            }
        }

        ctx && ctx.drawImage(img, 0, 0, drawWidth, drawHeight)

        canvas.toBlob(
            blob => {
                resolve(
                    new File([blob as Blob], file.name, {
                        type: file.type
                    })
                )
            },
            file.type,
            1
        )
    })
}

/**
 * 缩放图片文件到指定宽高
 * @param file
 * @param options
 * @example
 * ```ts
 * let formData = new FormData()
 * let resizeFile = await imageResize(file, { width: 800 })
 * formData.append('file', resizeFile)
 * ```
 */
export function imageResize(
    file: File,
    options: {
        width?: number
        height?: number
        type?: 'image/jpeg' | 'image/png' | 'image/webp' // 生成的图片格式， 默认 image/jpge
        encoderOptions?: number // 值在0与1之间，当请求图片格式为image/jpeg或者image/webp时用来指定图片展示质量。如果这个参数的值不在指定类型与范围之内，则使用默认值，其余参数将被忽略
    } = {}
) {
    let canvas = document.createElement('canvas')

    let { type, encoderOptions } = options
    const optHeight = options.height
    const optWidth = options.width
    if (!type) {
        type = 'image/jpeg'
    }
    if (!encoderOptions) {
        encoderOptions = 0.8
    }

    return new Promise<Blob>(async (resolve, reject) => {
        if (!optWidth && !optHeight) {
            return reject(new Error('width and height is null'))
        }
        let base64Data: string | null = await readFile(file)
        let img: HTMLImageElement | null = await buildImage(base64Data)

        if (!img.width || !img.height) {
            return reject(new Error('img error'))
        }
        let ctx = canvas.getContext('2d')
        // 等比缩放
        if (optWidth && !optHeight) {
            let imgWidthScale = optWidth / img.width
            canvas.width = optWidth
            canvas.height = img.height * imgWidthScale
        } else if (optHeight && !optWidth) {
            let imgHeightScale = optHeight / img.height
            canvas.height = optHeight
            canvas.width = img.width * imgHeightScale
        } else {
            canvas.width = Number(optWidth)
            canvas.height = Number(optHeight)
        }

        ctx && ctx.drawImage(img, 0, 0, canvas.width, canvas.height)

        canvas.toBlob(
            blob => {
                resolve(blob as Blob)
                img = null
                base64Data = null
                // @ts-ignore
                canvas = null
                ctx = null
            },
            type,
            encoderOptions
        )
    })
}
