/**
 * 调整 VueDraggable 配置的根容器
 * 以将拖拽目标重定向为上传文件列表项
 */

import Vue, { ComponentOptions } from 'vue'
import VueDraggable from 'vuedraggable'

interface CustomDraggableThisType extends Vue {
    isUploadList: boolean
}

const CustomDraggable: ComponentOptions<Vue> = {
    ...VueDraggable,
    name: 'custom-vue-draggable',
    data() {
        return {
            ...VueDraggable.data(),
            isUploadList: true
        }
    },
    computed: {
        ...VueDraggable.computed,
        rootContainer(this: CustomDraggableThisType) {
            return this.isUploadList
                ? this.$el.querySelector('.el-upload-list')
                : this.$el
        }
    },
    methods: {
        ...VueDraggable.methods,
        getChildrenNodes(this: CustomDraggableThisType) {
            const children = Array.from(
                // @ts-ignore
                this.$slots.default[0].child.$children[0].$el.children
            )
            return children.map(el => ({
                elm: el
            }))
        }
    }
}

// @ts-ignore
delete CustomDraggable['_Ctor']
// @ts-ignore
delete CustomDraggable['_compiled']

export default Vue.extend(CustomDraggable)
