/**
 * MobileCroppa 移动端裁剪组件
 * @author minfive <chenxiaowu1994@outlook.com>
 * @date 2019-07-30 18:03:19
 * @since 0.0.1
 */

import Vue from '@/components/base'
import { Component, Prop, Watch } from 'vue-property-decorator'
import template from './MobileCroppa.vue'
import Croppa from 'vue-croppa'

type ClipPlugin = (
    ctx: CanvasRenderingContext2D,
    x: number,
    y: number,
    w: number,
    h: number
) => void

interface SizeType {
    width: number
    height: number
}

type GenClipPlugin = (size: SizeType, quality: number) => ClipPlugin

/**
 * 圆形裁剪插件
 */
const clipRountPlugin: GenClipPlugin = (size, quality) => {
    return (ctx, x, y, w, h) => {
        ctx.beginPath()
        ctx.arc(
            x + w / 2,
            y + h / 2,
            (size.width * quality) / 2,
            0,
            2 * Math.PI,
            true
        )
        ctx.closePath()
    }
}

/**
 * 方形裁剪插件
 */
const clipSquarePlugin: GenClipPlugin = (size, quality) => {
    const { width, height } = size

    const canvasWidth = width * quality
    const canvasHeight = height * quality

    return (ctx, x, y, w, h) => {
        ctx.beginPath()
        ctx.rect(
            x + (w - canvasWidth) / 2,
            y + (h - canvasHeight) / 2,
            canvasWidth,
            canvasHeight
        )
        ctx.closePath()
    }
}

@Component({
    name: 'MobileCroppa',
    mixins: [template],
    components: {
        VueCroppa: Croppa.component
    }
})
export default class MobileCroppa extends Vue {
    public $refs!: {
        topCroppa: any
        offscreenCanvas: HTMLCanvasElement
    }
    //#region datas ******************************/
    protected currentCroppa: {
        addClipPlugin?: (plugin: ClipPlugin) => void
        refresh?: () => void
    } = {}
    //#endregion datas ***************************/

    //#region props ******************************/
    /**
     * 裁剪图片地址
     */
    @Prop({ required: true, type: String })
    protected readonly src!: string

    /**
     * 图片质量比
     */
    @Prop({ default: 2, type: Number })
    protected readonly quality!: number

    /**
     * 圆形裁剪
     */
    @Prop({ default: true, type: Boolean })
    protected readonly round!: boolean

    /**
     * 裁剪规格
     */
    @Prop({ default: '440*440', type: String })
    protected readonly cropSize!: string
    //#endregion props ***************************/

    //#region vuex *******************************/
    //#endregion vuex ****************************/

    //#region computed ***************************/
    protected get currentCropSize(): SizeType {
        const sizeArr = this.cropSize.split('*').map(str => Number(str))

        return {
            width: sizeArr[0],
            height: sizeArr[1]
        }
    }
    //#endregion computed ************************/

    //#region watchers ***************************/
    @Watch('src')
    protected changeSrc() {
        this.currentCroppa.refresh && this.currentCroppa.refresh()
    }
    //#endregion watchers ************************/

    //#region life cycle *************************/
    protected created() {}
    //#endregion life cycle **********************/

    //#region hooks ******************************/
    //#endregion hooks ***************************/

    //#region event handler **********************/
    protected onInit() {
        const cropSize = this.currentCropSize
        const quality = this.quality

        this.currentCroppa.addClipPlugin &&
            this.currentCroppa.addClipPlugin(
                this.round
                    ? clipRountPlugin(cropSize, quality)
                    : clipSquarePlugin(cropSize, quality)
            )
    }
    //#endregion event handler *******************/

    //#region methods ****************************/
    // 渲染离屏 canvas
    private renderOffscreenCanvas(formCanvas: HTMLCanvasElement) {
        const canvas = this.$refs.offscreenCanvas
        const ctx = canvas.getContext('2d')
        const quality = this.quality
        const { width, height } = this.currentCropSize

        canvas.width = width * quality
        canvas.height = height * quality

        if (ctx) {
            ctx.clearRect(0, 0, width, height)
            ctx.drawImage(
                formCanvas,
                (formCanvas.width - width * quality) / 2,
                (formCanvas.height - height * quality) / 2,
                canvas.width,
                canvas.height,
                0,
                0,
                canvas.width,
                canvas.height
            )
        }

        return canvas
    }

    private getOffscreenCanvas() {
        return this.renderOffscreenCanvas(
            this.$refs.topCroppa.$el.querySelector('canvas')
        )
    }

    /* tslint:disable */
    public generateDataUrl(type: string, compressionRate: number) {
        const canvas = this.getOffscreenCanvas()
        return canvas.toDataURL(type, compressionRate)
    }

    public generateBlob(
        callback: (blob: Blob | null) => void,
        mimeType: string,
        qualityArgument: number
    ) {
        const canvas = this.getOffscreenCanvas()
        return canvas.toBlob(callback, mimeType, qualityArgument)
    }

    public promisedBlob(
        mimeType: string,
        qualityArgument: number
    ): Promise<Blob | null> {
        return new Promise((resolve, reject) => {
            try {
                this.generateBlob(
                    blob => {
                        resolve(blob)
                    },
                    mimeType,
                    qualityArgument
                )
            } catch (err) {
                reject(err)
            }
        })
    }
    /* tslint:enable */
    //#endregion methods *************************/
}
