/**
 * CustomImageCrop 自定义图片裁剪
 * @author minfive <chenxiaowu1994@outlook.com>
 * @date 2019-07-30 14:52:04
 * @since 0.0.1
 */

import Vue from '@/components/base'
import { Component, Prop } from 'vue-property-decorator'
import template from './CustomImageCrop.vue'
import MobileCroppa from '../MobileCroppa'

interface SizeType {
    width: number
    height: number
}

const getImage: (src: string) => Promise<HTMLImageElement> = src => {
    return new Promise((resolve, reject) => {
        const image = new Image()
        image.onload = () => resolve(image)
        image.onerror = reject
        image.src = src
    })
}

@Component({
    name: 'CustomImageCrop',
    mixins: [template],
    components: {
        MobileCroppa
    }
})
export default class CustomImageCrop extends Vue {
    public $refs!: {
        croppa: MobileCroppa
    }

    //#region datas ******************************/
    protected show = false
    protected type = 'image/png'
    protected resolveFun!: (blob: Blob) => void
    protected rejectFun!: () => void
    protected src = 'https://zhanziyang.github.io/vue-croppa/static/500.jpeg'
    protected isInit = false
    protected quality = 2
    protected width = 440
    protected height = 768
    //#endregion datas ***************************/

    //#region props ******************************/
    @Prop({ default: '440*440', type: String })
    protected readonly cropSize!: string
    //#endregion props ***************************/

    //#region vuex *******************************/
    //#endregion vuex ****************************/

    //#region computed ***************************/
    protected get currentCropSize(): SizeType {
        const sizeArr = this.cropSize.split('*').map(str => Number(str))

        return {
            width: sizeArr[0],
            height: sizeArr[1]
        }
    }
    //#endregion computed ************************/

    //#region watchers ***************************/
    //#endregion watchers ************************/

    //#region life cycle *************************/
    protected created() {}
    //#endregion life cycle **********************/

    //#region hooks ******************************/
    //#endregion hooks ***************************/

    //#region event handler **********************/
    protected async submit() {
        if (this.resolveFun) {
            const blob = await this.$refs.croppa.promisedBlob(this.type, 1)
            if (blob) {
                this.resolveFun(blob)
                this.show = false
                delete this.resolveFun
                delete this.rejectFun
            }
        }
    }

    protected handleClose() {
        if (this.rejectFun) {
            this.rejectFun()
        }
    }
    //#endregion event handler *******************/

    //#region methods ****************************/
    private async init() {
        const image = await getImage(this.src)
        this.initSize(image)
        this.initQuality(image)
        this.isInit = true
    }

    /**
     * 初始化裁剪区宽高
     */
    private async initSize(image: HTMLImageElement) {
        const maxHeight = Math.floor(
            document.documentElement.clientHeight * 0.9
        )
        const maxWidth = Math.floor(document.documentElement.clientWidth * 0.7)
        const minHeight = this.currentCropSize.height
        const minWidth = this.currentCropSize.width
        const imageWidth = image.width
        const imageHeight = image.height
        const imageAspectRatio = imageWidth / imageHeight
        const cropSizeAspectRatio = minWidth / minHeight

        let resWidth = image.width
        let resHeight = image.height

        if (imageAspectRatio > cropSizeAspectRatio) {
            // 图片过宽，以最小宽度为基准
            resWidth = minWidth
            resHeight = minWidth / imageAspectRatio

            if (resHeight < minHeight) {
                resHeight = minHeight
                resWidth = minHeight * imageAspectRatio
            }
        } else {
            // 图片过长，以最小高度为基准
            resHeight = minHeight
            resWidth = minHeight * imageAspectRatio

            if (resWidth < minWidth) {
                resWidth = minWidth
                resHeight = minWidth / imageAspectRatio
            }
        }

        if (resHeight > maxHeight) {
            resHeight = maxHeight
            resWidth = imageAspectRatio * resHeight
        }

        if (resWidth > maxWidth) {
            resWidth = maxWidth
            resHeight = resWidth / imageAspectRatio
        }

        if (resHeight < minHeight) {
            resHeight = minHeight
        }

        if (resWidth < minWidth) {
            resWidth = minWidth
        }

        this.width = resWidth
        this.height = resHeight
    }

    /**
     * 初始化图片质量比
     */
    private async initQuality(image: HTMLImageElement) {
        const { width: imageWidth, height: imageHeight } = image
        const width = this.width
        const height = this.height
        const imageAspectRatio = imageWidth / imageHeight

        let realCanvasWidth = width

        if (width / imageWidth >= height / imageHeight) {
            // 以高为基准
            realCanvasWidth = imageAspectRatio * height
        } else {
            // 以宽为基准
            realCanvasWidth = width / imageAspectRatio
        }

        this.quality = realCanvasWidth / width
    }

    /* tslint:disable */
    public crop(src: string, type: string): Promise<Blob> {
        return new Promise(async (resolve, reject) => {
            this.resolveFun = resolve
            this.rejectFun = reject
            this.src = src
            this.type = type
            await this.init()
            this.show = true
        })
    }
    /* tslint:enable */
    //#endregion methods *************************/
}
