import Vue from '@/components/base'
import { Component, Prop, Watch } from 'vue-property-decorator'
import template from './UploadItem.vue'
import { ElUploadInternalFileDetail } from 'element-ui/types/upload'

export enum BaseActionCommand {
    Preview = 'preview',
    Delete = 'delete'
}

export type UploadType = 'file' | 'image' | 'video'

export type ActionHook = (file: ElUploadInternalFileDetail) => void

export interface ActionItem {
    icon: object
    command: BaseActionCommand | ActionHook
}

@Component({
    name: 'UploadItem',
    mixins: [template]
})
export default class UploadItem extends Vue {
    //#region datas ******************************/
    @Prop({ required: true, type: Object })
    protected readonly file!: ElUploadInternalFileDetail

    @Prop({ default: () => 'image', type: [String, Array] })
    protected readonly type!: UploadType | UploadType[]

    @Prop({ default: false, type: Boolean })
    protected readonly showName!: boolean

    @Prop({ default: () => [], type: Array })
    protected readonly actions!: ActionItem[]
    //#endregion datas ***************************/

    //#region props ******************************/
    //#endregion props ***************************/

    //#region vuex *******************************/
    //#endregion vuex ****************************/

    //#region computed ***************************/
    protected get rType() {
        let type = this.type
        if (Array.isArray(type)) {
            if (type.includes('image')) {
                return 'image'
            }
        }
        if (['image', 'video'].includes(type as UploadType)) {
            return 'image'
        }
        return type
    }

    protected get isVideo() {
        let file = this.file.raw
        if (file && file.type.match('video')) {
            return true
        }
        return false
    }
    //#endregion computed ************************/

    //#region watchers ***************************/
    //#endregion watchers ************************/

    //#region life cycle *************************/
    protected created() {}
    //#endregion life cycle **********************/

    //#region hooks ******************************/
    //#endregion hooks ***************************/

    //#region event handler **********************/
    //#endregion event handler *******************/

    //#region methods ****************************/
    //#endregion methods *************************/
}
