import Vue from '@/components/base'
import { Component } from 'vue-property-decorator'
import template from './UploadView.vue'

// import Upload, { ActionItem, BaseActionCommand } from '@/components/Upload'
import Upload, { ActionItem, BaseActionCommand } from '@/lib'
import CustomUpload from './components/CustomUpload'
import CustomImageCrop from './components/CustomImageCrop'
import { ElUploadInternalFileDetail } from 'element-ui/types/upload'

import searchSvg from '@icon/search.svg'
import shareSvg from '@icon/share.svg'
import deleteSvg from '@icon/delete.svg'

interface QiniuConfig {
    token: string
    domain: string
}

function getRandomStr(len: number = 10) {
    return Math.random()
        .toString(16)
        .slice(2, len + 3)
}

function sleep(time: number = 0) {
    return new Promise(resolve => {
        window.setTimeout(resolve, time)
    })
}

@Component({
    name: 'UploadView',
    mixins: [template],
    components: {
        CustomUpload,
        CustomImageCrop,
        Upload
    }
})
export default class UploadView extends Vue {
    public $refs!: {
        customImageCrop: CustomImageCrop
    }
    //#region datas ******************************/
    protected upload1 = []
    protected upload2 = []
    protected imageUrls = []
    protected imageUrls1 = []
    protected imageUrls2 = []
    protected imageUrls3 = []
    protected imageUrls4 = []
    protected imageUrls5 = []
    protected imageUrls6 = []
    protected compressionImageUrl = []
    protected unCompressionImageUrl = []
    protected actionDemoUrl = []
    protected hasNameImageUrls = []

    /**
     * 上传服务器地址
     */
    protected readonly qiniuUpHost =
        'https://www.mocky.io/v2/5185415ba171ea3a00704eed/posts'

    protected uploadKey = ''
    protected qiniuConfig: QiniuConfig | null = null
    //#endregion datas ***************************/

    //#region props ******************************/
    //#endregion props ***************************/

    //#region vuex *******************************/
    //#endregion vuex ****************************/

    //#region computed ***************************/
    protected get customActions(): ActionItem[] {
        return [
            {
                icon: searchSvg,
                command: BaseActionCommand.Preview
            },
            {
                icon: shareSvg,
                command: file => {
                    this.$alert(file.url || '')
                }
            },
            {
                icon: deleteSvg,
                command: BaseActionCommand.Delete
            }
        ]
    }

    protected get currentUploadData() {
        return {
            // token: this.qiniuConfig ? this.qiniuConfig.token : '',
            key: this.uploadKey
        }
    }

    /**
     * 上传队列，保证文件自定义 key 能串行使用
     */
    protected get uploadQueue() {
        const queue: Array<() => void> = []
        let isRuning = false

        const run = () => {
            const nextEvalFun = queue.shift()
            if (nextEvalFun) {
                isRuning = true
                nextEvalFun()
            } else {
                isRuning = false
            }
        }

        return () => {
            return new Promise(resolve => {
                queue.push(() => {
                    resolve()

                    // 等待 100 毫秒，保证前一个上传请求已发出
                    window.setTimeout(run, 100)
                })

                !isRuning && run()
            })
        }
    }
    //#endregion computed ************************/

    //#region watchers ***************************/
    //#endregion watchers ************************/

    //#region life cycle *************************/
    protected created() {}
    //#endregion life cycle **********************/

    //#region hooks ******************************/
    //#endregion hooks ***************************/

    //#region event handler **********************/
    protected async beforeUpload(file: ElUploadInternalFileDetail) {
        // this.qiniuConfig = await getToken()

        await this.uploadQueue()
        this.uploadKey = getRandomStr(20) + file.uid

        return file
    }

    protected onUploadSuccess(res: any) {
        // const url = (this.qiniuConfig as QiniuConfig).domain + res.key
        const url = 'https://iph.href.lu/800x800'

        return url
    }

    protected async onCustomCrop(file: File) {
        const type = 'image/png'
        const blob = await this.$refs.customImageCrop.crop(
            URL.createObjectURL(file),
            type
        )

        return new File([blob], file.name, {
            lastModified: file.lastModified,
            type
        })
    }
    //#endregion event handler *******************/

    //#region methods ****************************/
    //#endregion methods *************************/
}
